import { useEffect, useState } from "react";
import { useRouter } from "next/router";

interface VendorStats {
  totalBookings: number;
  totalRevenue: number;
  averageRating: number;
  activeServices: number;
}

export default function VendorDashboard() {
  const [vendorData, setVendorData] = useState<VendorStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  useEffect(() => {
    const fetchVendorData = async () => {
      const response = await fetch("/api/vendor/stats");
      if (!response.ok) {
        setError("Failed to fetch vendor data.");
        setLoading(false);
        return;
      }
      const data = await response.json();
      setVendorData(data);
      setLoading(false);
    };

    const sessionId = localStorage.getItem("sessionId");
    if (!sessionId) {
      router.push("/login");
    } else {
      fetchVendorData();
    }
  }, [router]);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>{error}</div>;

  return (
    <div>
      <h1>Vendor Dashboard</h1>
      {vendorData && (
        <div>
          <p>Total Bookings: {vendorData.totalBookings}</p>
          <p>Total Revenue: ${vendorData.totalRevenue}</p>
          <p>Average Rating: {vendorData.averageRating}/5</p>
          <p>Active Services: {vendorData.activeServices}</p>
        </div>
      )}
    </div>
  );
}
